  display: block;
}

.job-description ol, .job-description ul {
  list-style: none;
}

.job-description blockquote, q {
  quotes: none;
}

.job-description blockquote:before, .job-description blockquote:after,
.job-description q:before, .job-description q:after {
  content: '';
  content: none;
}

.job-description table {
  border-collapse: collapse;
  border-spacing: 0;
}

.job-description > *, .job-description span, .job-description li, .job-description strong {
  color: rgba(31, 41, 55) !important;
  font-size: 15px !important;
  font-family: Arial, Helvetica, sans-serif !important;
}

.job-description h1,
.job-description h2,
.job-description h3,
.job-description h4,
.job-description h5,
.job-description h6,
.job-description span,
.job-description strong,
.job-description .h1,
.job-description .h2,
.job-description .h3,
.job-description .h4,
.job-description .h5,
.job-description .h6 {
  font-weight: bold !important;
}

.job-description a {
  text-decoration: underline !important;
}

.job-description li {
  list-style-type: circle !important;
  margin-left: 1rem !important;
}

.filters {
  display: none;
}

.job:last-of-type {
  border: none;
}

.App {
  min-height: 100vh;
  grid-template-rows: auto 1fr;
}

@media (min-width: 768px) {
  .App {
    max-width: 1920px;
    margin: 0 auto;
    grid-template-rows: 1fr auto;
  }
}

@media (min-width: 1920px) {
  header {
    width: 1920px !important;
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
  }
}

.dropdown {
  display: inline-block;
  position: relative;
}

.dropdown-content li {
  padding: 0.5rem;
  text-align: center;
}

.dropdown-content li:first-of-type {
  border-bottom: 1px solid white;
  background-color: #3b82f6
}

.dropdown-content li:last-of-type {
  background-color: red;
}

.dropdown-content li:hover:first-of-type {
  background-color: #2f6cf0
}

.dropdown-content li:hover:last-of-type {
  background-color: rgb(231, 2, 2);
}

.dropdown:hover .dropdown-content {
  display: block;
}

